  height: auto;
}
ul,
ol,
li {
  list-style: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input[type="text"],
input[type="password"],
div,
a {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  border-radius: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
.text-hide {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
em,
i,
b {
  font-style: normal;
}
fieldset,
img {
  border: 0;
  display: block;
}
input,
textarea,
select {
  *font-size: 100%;
}
audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
abbr,
acronym {
  border: 0;
  font-variant: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
.clear:after {
  content: " ";
  display: block;
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.browser-tips {
  background: #ffc;
  color: #626262;
  font-size: 14px;
  text-align: center;
  padding: 10px 0;
  /*position: absolute;*/
  /*top: 42px;*/
  width: 100%;
  height: 34px;
  line-height: 34px;
  left: 0px;
  right: 0px;
  z-index: 999;
}
.hd {
  display: block;
  text-indent: -9999px;
  color: rgba(0, 0, 0, 0);
}
.hide {
  display: none;
}
